/**
 * Character Card Component
 * =========================
 * Displays a character summary with portrait and actions
 */

'use client';

import Image from 'next/image';
import Link from 'next/link';
import { cn } from '@/lib/utils';
import type { CharacterSummary } from '@/types';

const FALLBACK_AVATAR = 'data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="180" height="180"><rect width="100%" height="100%" fill="%23053357"/><text x="50%" y="52%" dominant-baseline="middle" text-anchor="middle" font-size="44" fill="white" font-family="Arial">?</text></svg>';

interface CharacterCardProps {
  character: CharacterSummary;
  onDelete?: (id: string, name: string) => void;
  isDeleting?: boolean;
}

export function CharacterCard({ character, onDelete, isDeleting }: CharacterCardProps) {
  const handleDelete = (e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();
    onDelete?.(character.id, character.name);
  };

  return (
    <Link
      href={`/characters/${character.id}`}
      className={cn(
        'group relative block rounded-lg overflow-hidden bg-white shadow-md',
        'hover:shadow-lg transition-shadow duration-200',
        isDeleting ? 'opacity-50 pointer-events-none' : ''
      )}
    >
      {/* Portrait */}
      <div className="relative aspect-[3/4] bg-primary-800">
        <Image
          src={character.portrait || FALLBACK_AVATAR}
          alt={character.name}
          fill
          className="object-cover"
          unoptimized
        />
        
        {/* Delete button */}
        {onDelete ? (
          <button
            onClick={handleDelete}
            className="absolute top-2 right-2 w-8 h-8 rounded-full bg-red-500/80 hover:bg-red-600 text-white flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity"
            title="Delete character"
          >
            <XIcon className="w-5 h-5" />
          </button>
        ) : null}
      </div>

      {/* Info */}
      <div className="p-4">
        <h3 className="font-bold text-lg text-gray-900 uppercase truncate">
          {character.name}
        </h3>
        <div className="flex items-center gap-2 mt-1 text-sm text-gray-600">
          <span>Level {character.level}</span>
          {character.archetypeName ? (
            <>
              <span></span>
              <span className="truncate">{character.archetypeName}</span>
            </>
          ) : null}
        </div>
        {character.ancestryName ? (
          <p className="text-sm text-gray-500 mt-1 truncate">
            {character.ancestryName}
          </p>
        ) : null}
      </div>
    </Link>
  );
}

interface AddCharacterCardProps {
  onClick?: () => void;
}

export function AddCharacterCard({ onClick }: AddCharacterCardProps) {
  return (
    <button
      onClick={onClick}
      className="group flex flex-col items-center justify-center rounded-lg border-2 border-dashed border-gray-300 bg-gray-50 hover:border-primary-400 hover:bg-primary-50 transition-colors min-h-[280px]"
    >
      <div className="w-16 h-16 rounded-full bg-gray-200 group-hover:bg-primary-100 flex items-center justify-center transition-colors">
        <PlusIcon className="w-8 h-8 text-gray-400 group-hover:text-primary-600" />
      </div>
      <span className="mt-4 font-semibold text-gray-500 group-hover:text-primary-600">
        Add Character
      </span>
    </button>
  );
}

function XIcon({ className }: { className?: string }) {
  return (
    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor" className={className}>
      <path strokeLinecap="round" strokeLinejoin="round" d="M6 18 18 6M6 6l12 12" />
    </svg>
  );
}

function PlusIcon({ className }: { className?: string }) {
  return (
    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor" className={className}>
      <path strokeLinecap="round" strokeLinejoin="round" d="M12 4.5v15m7.5-7.5h-15" />
    </svg>
  );
}
